"use client";

interface StockTableProps {
    title: string;
    stocks: any[];
}

export default function StockTable({ title, stocks }: StockTableProps) {
    return (
        <div className="bg-white shadow rounded-lg p-4">
            <h2 className="text-lg font-semibold">{title}</h2>
            <table className="w-full mt-2">
                <thead><tr>
                        <th className="text-left">Ticker</th>
                        <th className="text-right">Price</th>
                        <th className="text-right">Change</th>
                    </tr>
                </thead>
                <tbody>
                    {stocks.map((stock, index) => (
                        <tr key={stock.ticker && stock.date ? `${stock.ticker}-${stock.date}` : `fallback-${index}`}>
                        <td className="text-left">{stock.Ticker}</td>
                        <td className="text-right">{stock.Close.toFixed(2)}</td>
                        <td className="text-right">{stock.Change.toFixed(2)}%</td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
}

